/* -------------------------------------------------
>> Base Styles
---------------------------------------------------
>> Do not edit these styles
------------------------------------------------- */

body {
	position: relative;
}

/* -------------------------------------------------
>> Project Styles
---------------------------------------------------
>> Edit these styles as required
------------------------------------------------- */

body {

	@include font(bt, reg, family weight);
	@include font-size(2);

	color: map-get( $map: $colors, $key: black );
	line-height: 1.6;
}

p {
	margin: 0 0 3rem;
}

::selection {
	color: map-get( $map: $base_colors, $key: selection-color );
	background-color: map-get( $map: $base_colors, $key: selection-bgcolor );
}
